<template>
  <div v-if="isLoggedIn" class="container">
    <h1>Gestão de Pneus</h1>

    <div class="search-container">
      <input v-model="searchQuery" type="text" placeholder="Pesquisar..." />
      <button @click="adicionarPneu">Adicionar Pneu</button>
      <button @click="gerarPdf">Gerar PDF</button>
    </div>

    <!-- Popup de Detalhes do Pneu (Editar/Eliminar) -->
    <div v-if="mostrarCriarPneu || mostrarPneuDetalhes" class="popup-overlay">
      <div class="popup-content">
        <button @click="fecharPopup" class="close-btn">X</button>

        <h2 v-if="mostrarCriarPneu">Adicionar Pneu</h2>
        <h2 v-if="mostrarPneuDetalhes">Detalhes do Pneu</h2>

        <div class="form-group">
          <label>Medida:</label>
          <input v-model="selecionadoPneu.tamanho" type="text" @input="formatarTamanho" />
        </div>

        <div class="form-group">
          <label>Comercial:</label>
          <input v-model="selecionadoPneu.comercial" type="checkbox" />
        </div>

        <div class="form-group">
          <label>
            Localização:
            <div class="location-input-container">
              <input v-model="selecionadoPneu.localizacao" type="text" @input="validarLocalizacao" placeholder="A1, B2, etc." />
              <span class="tooltip-container">
                <i class="info-icon">i</i>
                <img :src="getPlantaImage(selecionadoPneu.localizacao)" alt="Planta" class="tooltip-image" />
              </span>
            </div>
          </label>
        </div>

        <div class="form-group">
          <label>Marca:</label>
          <select v-model="selecionadoPneu.marca">
            <option v-for="marca in marcas" :key="marca">{{ marca }}</option>
          </select>
        </div>

        <div class="form-group">
          <label>Modelo:</label>
          <input v-model="selecionadoPneu.modelo" type="text" />
        </div>

        <div class="form-group">
          <label>Quantidade:</label>
          <input v-model="selecionadoPneu.quantidade" type="number" />
        </div>

        <div class="form-group">
          <label>Preço:</label>
          <input v-model="selecionadoPneu.preco" type="number" min="0" step="any" />
        </div>

        <div v-if="mostrarPneuDetalhes" class="button-group">
          <button @click="eliminarPneu(selecionadoPneu.id)" :disabled="loading">Eliminar</button>
          <button @click="salvarAlteracoes" :disabled="loading">Salvar Alterações</button>
        </div>
        
        <button v-if="!mostrarPneuDetalhes" @click="salvarAlteracoes" :disabled="loading">{{ mostrarPneuDetalhes ? 'Salvar Alterações' : 'Submeter' }}</button>
      </div>
    </div>

    <!-- Tabela de Resultados -->
    <table v-if="pneus.length">
      <thead>
        <tr>
          <th @click="ordenar('tamanho')">
            Medida <span v-if="ordemColuna === 'tamanho'">{{ ordemAscendente ? '↑' : '↓' }}</span>
          </th>
          <th @click="ordenar('marca')">
            Marca <span v-if="ordemColuna === 'marca'">{{ ordemAscendente ? '↑' : '↓' }}</span>
          </th>
          <th @click="ordenar('modelo')">
            Modelo <span v-if="ordemColuna === 'modelo'">{{ ordemAscendente ? '↑' : '↓' }}</span>
          </th>
          <th @click="ordenar('localizacao')">
            Localização <span v-if="ordemColuna === 'localizacao'">{{ ordemAscendente ? '↑' : '↓' }}</span>
          </th>
          <th @click="ordenar('quantidade')">
            Quantidade <span v-if="ordemColuna === 'quantidade'">{{ ordemAscendente ? '↑' : '↓' }}</span>
          </th>
          <th @click="ordenar('preco')">
            Preço <span v-if="ordemColuna === 'preco'">{{ ordemAscendente ? '↑' : '↓' }}</span>
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="pneu in pneus" :key="pneu.id" @click="mostrarDetalhesPneu(pneu)" @mouseenter="adjustTooltipPosition($event)">
          <td>{{ formatarTamanhoExibicao(pneu.tamanho) }}{{ pneu.comercial ? ' C' : '' }}</td>
          <td>{{ pneu.marca }}</td>
          <td>{{ pneu.modelo }}</td>
          <td class="location-cell">
            <span>{{ pneu.localizacao }}</span>
            <span class="tooltip-container">
              <i class="info-icon">i</i>
              <img :src="getPlantaImage(pneu.localizacao)" alt="Planta" class="tooltip-image" />
            </span>
          </td>
          <td>{{ pneu.quantidade }}</td>
          <td>{{ formatarPreco(pneu.preco) }}</td>
        </tr>
      </tbody>
    </table>
    <p v-else>Nenhum pneu encontrado.</p>
  </div>
  <div v-else>
    <p>Redirecionando para a página de login...</p>
  </div>
</template>

<script>
import axios from "axios";
import { jwtDecode } from "jwt-decode";
import "@/assets/styles/global.css";

export default {
  data() {
    return {
      pneus: [],
      searchQuery: "",
      mostrarCriarPneu: false,
      mostrarPneuDetalhes: false,
      selecionadoPneu: null,
      marcas: [
        "APLUS", "ATLAS", "BARUM", "BRIDGESTONE", "CHAO YANG", "COMFORSER", 
        "CONTINENTAL", "COOPER", "DELMAX", "DUNLOP", "FALKEN", "FEDIMA", 
        "FIREMAX", "FIRESTONE", "FORTUNA", "GITI", "GOODRICH", "GOOD RIDE", 
        "GOODYEAR", "GRENLANDER", "HABILEAD", "HAIDA", "HANKOOK", "INTERSTATE", 
        "JOURNEY", "KELLY TIRES", "KUMHO", "LINGLONG", "MABOR", "MARSHAL", 
        "MAXTREK", "MAZZINI", "METZELER", "MICHELIN", "MILEKING", "MINNELL", 
        "NANKANG", "NEXEN", "PIRELLI", "RADAR", "SECURITY", "SUPERIA", 
        "TOYO", "TRACMAX", "TRIANGLE", "VREDESTEIN", "WESTLAKE", "WINDFORCE", 
        "WINRUN", "YOKOHAMA"
      ],
      ordemColuna: "",
      ordemAscendente: true,
      loading: false,
      isLoggedIn: false
    };
  },
  created() {
    this.checkLoginStatus();
    this.listarPneus();
  },
  methods: {
    checkLoginStatus() {
      const token = localStorage.getItem("token");
      if (token) {
        try {
          const decoded = jwtDecode(token);
          this.isLoggedIn = !!decoded;
        } catch (error) {
          console.error("Erro ao decodificar o token:", error);
          this.isLoggedIn = false;
          this.$router.push("/login");
        }
      } else {
        this.isLoggedIn = false;
        this.$router.push("/login");
      }
    },
    async listarPneus() {
      this.loading = true;
      try {
        const { data } = await axios.get("http://localhost:8000/pneus/");
        this.pneus = data;
      } catch (error) {
        console.error("Erro ao listar pneus:", error);
      } finally {
        this.loading = false;
      }
    },

    async eliminarPneu(id) {
      const confirmar = window.confirm("Tem certeza que deseja eliminar este pneu?");
      if (confirmar) {
        try {
          this.loading = true;
          const token = localStorage.getItem("token");
          await axios.delete(`http://localhost:8000/pneus/${id}/`, {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          });
          this.fecharPopup();
          this.listarPneus();
        } catch (error) {
          console.error("Erro ao eliminar pneu:", error);
        } finally {
          this.loading = false;
        }
      }
    },

    mostrarDetalhesPneu(pneu) {
      this.selecionadoPneu = { ...pneu };
      this.mostrarPneuDetalhes = true;
    },

    adicionarPneu() {
      this.selecionadoPneu = {
        marca: "",
        modelo: "",
        tamanho: "",
        preco: null,
        quantidade: null,
        comercial: false,
        localizacao: ""
      };
      this.mostrarCriarPneu = true;
    },

    fecharPopup() {
      this.mostrarCriarPneu = false;
      this.mostrarPneuDetalhes = false;
      this.selecionadoPneu = null;
    },

    salvarAlteracoes() {
      // Lógica para salvar as alterações ou criar um novo pneu
      if (this.selecionadoPneu.id) {
        this.atualizarPneu();
      } else {
        this.criarPneu();
      }
    },

    async atualizarPneu() {
      try {
        const token = localStorage.getItem("token"); // Pegue o token do localStorage (ou de outro local onde esteja armazenado)
        await axios.put(
          `http://localhost:8000/pneus/${this.selecionadoPneu.id}/`,
          this.selecionadoPneu,
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        );
        this.fecharPopup();
        this.listarPneus();
      } catch (error) {
        console.error("Erro ao atualizar pneu:", error);
      }
    },
    async criarPneu() {
      try {
        const token = localStorage.getItem("token");
        await axios.post("http://localhost:8000/pneus/", this.selecionadoPneu, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });
        this.fecharPopup();
        this.listarPneus();
      } catch (error) {
        console.error("Erro ao criar pneu:", error);
      }
    },

    formatarTamanho() {
      this.selecionadoPneu.tamanho = this.selecionadoPneu.tamanho.replace(/\D/g, '').slice(0, 7);
    },

    formatarTamanhoExibicao(tamanho) {
      return tamanho ? tamanho.replace(/(\d{3})(\d{2})(\d{2})/, "$1/$2/$3") : "";
    },

    formatarPreco(preco) {
      return preco ? `${preco.toFixed(2)} €` : "";
    },

    getPlantaImage(localizacao) {
      try {
        return require(`@/assets/plantas/planta_${localizacao[0].toLowerCase()}.png`);
      } catch (e) {
        return require('@/assets/plantas/planta_desconhecida.png'); // Caminho para uma imagem padrão
      }
    },

    validarLocalizacao() {
      this.selecionadoPneu.localizacao = this.selecionadoPneu.localizacao.toUpperCase();
      const regex = /^[A-K][1-4]?$|^[L-R]$/;
      if (!regex.test(this.selecionadoPneu.localizacao)) {
        this.selecionadoPneu.localizacao = this.selecionadoPneu.localizacao.slice(0, -1);
      }
    },

    ordenar(coluna) {
      if (this.ordemColuna === coluna) {
        this.ordemAscendente = !this.ordemAscendente;
      } else {
        this.ordemColuna = coluna;
        this.ordemAscendente = true;
      }

      this.pneus.sort((a, b) => {
        let valorA = a[coluna];
        let valorB = b[coluna];

        if (typeof valorA === "string") {
          valorA = valorA.toLowerCase();
          valorB = valorB.toLowerCase();
        }

        if (valorA < valorB) return this.ordemAscendente ? -1 : 1;
        if (valorA > valorB) return this.ordemAscendente ? 1 : -1;
        return 0;
      });
    },

    adjustTooltipPosition(event) {
      const tooltipImage = event.target.querySelector('.tooltip-image');
      const rect = event.target.getBoundingClientRect();
      const windowHeight = window.innerHeight;

      if (rect.bottom + tooltipImage.offsetHeight > windowHeight) {
        tooltipImage.style.top = 'auto';
        tooltipImage.style.bottom = '100%';
        tooltipImage.style.marginBottom = '10px';
      } else {
        tooltipImage.style.top = '-10px';
        tooltipImage.style.bottom = 'auto';
        tooltipImage.style.marginBottom = '0';
      }
    },

    gerarPdf() {
      // Função para gerar PDF (a ser implementada)
    }
  }
};
</script>

