import { createRouter, createWebHistory } from 'vue-router';
import HomePage from '@/views/HomePage.vue';
import PneusList from '@/views/PneusList.vue';
import LoginPage from '@/views/LoginPage.vue';
import ResultadoPesquisa from "@/views/ResultadoPesquisa.vue";
import ContaPage from "@/views/ContaPage.vue"; // Importa a nova página

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomePage
  },
  {
    path: '/gestaostock',
    name: 'pneus',
    component: PneusList
  },
  {
    path: '/login',
    name: 'login',
    component: LoginPage
  },
  {
    path: "/pesquisa",
    name: "Pesquisa",
    component: ResultadoPesquisa,
  },
  {
    path: "/conta",
    name: "Conta",
    component: ContaPage, // Adiciona a nova rota
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
});

export default router;