<template>
  <div class="login-window">
    <div class="container">
      <h2>Login</h2>
      <form @submit.prevent="fazerLogin">
        <div class="form-group">
          <label for="username">Usuário:</label>
          <input v-model="username" type="text" id="username" required />
        </div>
        <div class="form-group">
          <label for="password">Senha:</label>
          <input v-model="password" type="password" id="password" required />
        </div>
        <div class="button-group">
          <button type="submit">Entrar</button>
        </div>
      </form>
      <p v-if="erro" style="color: red">{{ erro }}</p>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      username: "marcia", // 🔥 Você pode deixar vazio "" e preencher manualmente
      password: "marcia",
      erro: "",
    };
  },
  methods: {
    async fazerLogin() {
      try {
        const response = await axios.post("http://127.0.0.1:8000/users/login/", {
          nome: this.username,
          senha: this.password,
        });

        const token = response.data.access_token;
        localStorage.setItem("token", token);
        window.dispatchEvent(new Event("tokenChanged"));

        console.log("Login bem-sucedido! Token salvo.");
        this.$router.push("/"); // ✅ Redireciona para a página inicial
      } catch (error) {
        console.error("Erro ao fazer login:", error);
        this.erro = "Usuário ou senha inválidos!";
      }
    },
  },
};
</script>

<style scoped>
/* Estilos específicos para o LoginWindow */
.login-window {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh; /* Ocupa toda a altura da viewport */
  margin-top: -90px; /* Ajuste este valor conforme necessário */
}

.login-window .container {
  max-width: 600px;
  width: 100%;
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

.login-window .form-group {
  margin-bottom: 15px;
}

.login-window .button-group {
  display: flex;
  justify-content: center;
  width: 100%;
}

.login-window button {
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  background-color: #f67332; /* Laranja */
  color: white;
  cursor: pointer;
}

.login-window button:hover {
  background-color: #e5621f; /* Laranja escuro */
}

.login-window .container h2 {
  text-align: center;
  width: 100%;
}
</style>
