<template>
  <div class="container">
    <h1>Resultados da Pesquisa</h1>

    <p v-if="Object.keys($route.query).length">Parâmetros: {{ $route.query }}</p>
    <p v-else>Nenhum parâmetro informado.</p>

    <!-- Tabela de Resultados -->
    <table v-if="pneus.length">
      <thead>
        <tr>
          <th @click="ordenar('tamanho')">
            Medida <span v-if="ordemColuna === 'tamanho'">{{ ordemAscendente ? '↑' : '↓' }}</span>
          </th>
          <th @click="ordenar('marca')">
            Marca <span v-if="ordemColuna === 'marca'">{{ ordemAscendente ? '↑' : '↓' }}</span>
          </th>
          <th @click="ordenar('modelo')">
            Modelo <span v-if="ordemColuna === 'modelo'">{{ ordemAscendente ? '↑' : '↓' }}</span>
          </th>
          <th v-if="isLoggedIn" @click="ordenar('localizacao')">
            Localização <span v-if="ordemColuna === 'localizacao'">{{ ordemAscendente ? '↑' : '↓' }}</span>
          </th>
          <th @click="ordenar('quantidade')">
            Quantidade <span v-if="ordemColuna === 'quantidade'">{{ ordemAscendente ? '↑' : '↓' }}</span>
          </th>
          <th @click="ordenar('preco')">
            Preço <span v-if="ordemColuna === 'preco'">{{ ordemAscendente ? '↑' : '↓' }}</span>
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="pneu in pneus" :key="pneu.id">
          <td>{{ formatarTamanhoExibicao(pneu.tamanho) }}{{ pneu.comercial ? ' C' : '' }}</td>
          <td>{{ pneu.marca }}</td>
          <td>{{ pneu.modelo }}</td>
          <td v-if="isLoggedIn" class="location-cell">
            <span>{{ pneu.localizacao }}</span>
            <span class="tooltip-container">
              <i class="info-icon">i</i>
              <img :src="getPlantaImage(pneu.localizacao)" alt="Planta" class="tooltip-image" />
            </span>
          </td>
          <td>{{ pneu.quantidade }}</td>
          <td>{{ formatarPreco(pneu.preco) }}</td>
        </tr>
      </tbody>
    </table>
    <p v-else>Nenhum pneu encontrado.</p>
  </div>
</template>

<script>
import axios from "axios";
import { jwtDecode } from "jwt-decode";
import "@/assets/styles/global.css";

export default {
  data() {
    return {
      pneus: [],
      ordemColuna: "",
      ordemAscendente: true,
      loading: false,
      isLoggedIn: false
    };
  },
  created() {
    this.buscarResultados();
    this.checkLoginStatus();
  },
  methods: {
    async buscarResultados() {
      this.loading = true;
      try {
        const params = this.$route.query;
        const response = await axios.get("http://localhost:8000/pneus/", { params });
        let pneus = response.data;

        // Filtra os resultados com base na jante e nos últimos dois dígitos do tamanho
        if (params.jante) {
          pneus = pneus.filter(pneu => pneu.tamanho.slice(-2) === params.jante);
        }

        // Filtra os resultados com base na medida
        if (params.medida) {
          pneus = pneus.filter(pneu => pneu.tamanho === params.medida);
        }

        // Filtra os resultados com base no comercial, se o parâmetro estiver presente
        if (params.comercial !== undefined) {
          pneus = pneus.filter(pneu => pneu.comercial === (params.comercial === 'true'));
        }

        this.pneus = pneus;
      } catch (error) {
        console.error("Erro ao buscar resultados:", error);
      } finally {
        this.loading = false;
      }
    },
    ordenar(coluna) {
      if (this.ordemColuna === coluna) {
        this.ordemAscendente = !this.ordemAscendente;
      } else {
        this.ordemColuna = coluna;
        this.ordemAscendente = true;
      }

      this.pneus.sort((a, b) => {
        let valorA = a[coluna];
        let valorB = b[coluna];

        if (typeof valorA === "string") {
          valorA = valorA.toLowerCase();
          valorB = valorB.toLowerCase();
        }

        if (valorA < valorB) return this.ordemAscendente ? -1 : 1;
        if (valorA > valorB) return this.ordemAscendente ? 1 : -1;
        return 0;
      });
    },
    formatarTamanhoExibicao(tamanho) {
      return tamanho ? tamanho.replace(/(\d{3})(\d{2})(\d{2})/, "$1/$2/$3") : "";
    },
    formatarPreco(preco) {
      return preco ? `${preco.toFixed(2)} €` : "";
    },
    getPlantaImage(localizacao) {
      try {
        return require(`@/assets/plantas/planta_${localizacao.toLowerCase()}.png`);
      } catch (e) {
        return require('@/assets/plantas/planta_desconhecida.png'); // Caminho para uma imagem padrão
      }
    },
    checkLoginStatus() {
      const token = localStorage.getItem("token");
      if (token) {
        try {
          const decoded = jwtDecode(token);
          this.isLoggedIn = !!decoded;
        } catch (error) {
          console.error("Erro ao decodificar o token:", error);
          this.isLoggedIn = false;
        }
      } else {
        this.isLoggedIn = false;
      }
    }
  }
};
</script>

<style scoped>
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

h1 {
  text-align: center;
  color: #f67332; /* Laranja */
  margin-bottom: 20px;
}

button {
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  background-color: #f67332; /* Laranja */
  color: white;
  cursor: pointer;
  margin: 10px 0;
}

button:hover {
  background-color: #e5621f; /* Laranja escuro */
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

th, td {
  border: 1px solid #ddd;
  padding: 10px;
  text-align: left;
}

th {
  background-color: #f67332; /* Laranja */
  color: white;
  cursor: pointer;
}

th:hover {
  background-color: #e5621f; /* Laranja escuro */
}

tr:nth-child(even) {
  background-color: #f9f9f9;
}

tr:hover {
  background-color: #f1f1f1;
}

.tooltip-container {
  position: relative;
  display: inline-block;
}

.tooltip-container .tooltip-image {
  position: absolute;
  top: -10px;
  left: 100%;
  width: 150px;
  height: auto;
  display: none;
  z-index: 10;
}

.tooltip-container:hover .tooltip-image {
  display: block;
}

.info-icon {
  cursor: pointer;
  color: #f67332; /* Laranja */
  font-size: 16px;
}

.location-cell {
  position: relative;
}
</style>