<template>
  <div v-if="isLoggedIn" class="account-page">
    <div class="container">
      <h2>Gerenciar Conta</h2>
      <form @submit.prevent="atualizarConta">
        <div class="form-group">
          <label for="nome">Novo nome de usuário:</label>
          <input v-model="nome" type="text" id="nome" required />
        </div>
        <div class="form-group">
          <label for="senha">Nova Senha:</label>
          <input v-model="senha" type="password" id="senha" required />
        </div>
        <div class="button-container">
          <div class="criar-usuario-button">
            <button @click="mostrarCriarUsuario = true">Criar Novo Usuário</button>
          </div>
          <div class="button-group">
            <button type="submit">Atualizar</button>
          </div>
        </div>
      </form>
    </div>

    <!-- Popup para criar novo usuário -->
    <div v-if="mostrarCriarUsuario" class="popup-overlay">
      <div class="popup-content">
        <button @click="mostrarCriarUsuario = false" class="close-btn">X</button>
        <h2>Criar Novo Usuário</h2>
        <form @submit.prevent="criarUsuario">
          <div class="form-group">
            <label for="novoNome">Nome:</label>
            <input v-model="novoNome" type="text" id="novoNome" required />
          </div>
          <div class="form-group">
            <label for="novaSenha">Senha:</label>
            <input v-model="novaSenha" type="password" id="novaSenha" required />
          </div>
          <div class="button-group">
            <button type="submit">Criar</button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      nome: "",
      senha: "",
      mostrarCriarUsuario: false,
      novoNome: "",
      novaSenha: "",
      isLoggedIn: false
    };
  },
  created() {
    this.checkLoginStatus();
  },
  methods: {
    checkLoginStatus() {
      const token = localStorage.getItem("token");
      if (token) {
        try {
          const decoded = jwtDecode(token);
          this.isLoggedIn = !!decoded;
        } catch (error) {
          console.error("Erro ao decodificar o token:", error);
          this.isLoggedIn = false;
          this.$router.push("/login");
        }
      } else {
        this.isLoggedIn = false;
        this.$router.push("/login");
      }
    },
    async atualizarConta() {
      try {
        const token = localStorage.getItem("token");
        await axios.put(
          "http://localhost:8000/users/me",
          { nome: this.nome, senha: this.senha },
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        );
        alert("Conta atualizada com sucesso!");
      } catch (error) {
        console.error("Erro ao atualizar conta:", error);
        alert("Erro ao atualizar conta.");
      }
    },
    async criarUsuario() {
      try {
        const token = localStorage.getItem("token");
        await axios.post(
          "http://localhost:8000/users/",
          { nome: this.novoNome, senha: this.novaSenha },
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        );
        alert("Usuário criado com sucesso!");
        this.mostrarCriarUsuario = false;
        this.novoNome = "";
        this.novaSenha = "";
      } catch (error) {
        console.error("Erro ao criar usuário:", error);
        alert("Erro ao criar usuário.");
      }
    },
  },
};
</script>

